body{
  background-color: aliceblue;
}
.App{
  background-color: skyblue;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#to-do-form input {
  border:0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 5px;
  color: black;
}
#to-do-form input::placeholder{
  color: black;
}
#to-do-form input:focus,#to-do-form button:focus{
  outline: none;
}
#to-do-form button{
  border-radius: 5px;
  height: 50px;
  width: 80px;
  border: 0;
  background-color: blue;
  color: azure;
  cursor: pointer;
  font-weight: bolder;
}
